.nav__outer {
  width: 3000px;
  background: #FED978;
  color: rgb(0, 0, 0);
  overflow: auto;
  position: relative;
  z-index: 300000;
}

.navContainer {
  width: 60vw;
  max-width: 1200px;
  margin: auto;
  position: relative;
  color: rgb(0, 0, 0);
  z-index: 300000;
  padding: 0;
}

.nav__logoContainer {
  display: inline-block;
}

.nav__logo {
  height: 75px;
  float: left;
}

.nav__rightContainer {
  float: right;
  margin-right: -40px;
  color: rgb(0, 0, 0);
}

.nav__rightContainer__link {
  display: inline-block;
  margin: 0;
  padding: 25px 40px;
  color: rgb(0, 0, 0);
  font-family: "Lato", serif;
  font-weight: 600;
  font-size: 14px;
}

.nav__rightContainer__link:hover {
  background: #ebc86f;
  cursor: pointer;
}

.nav__active {
  background: #e2c16b;
}

.nav__hamburger {
  padding: 0 40px;
  line-height: 60px;
  height: 60px;
  font-size: 25px;
  color: rgb(0, 0, 0);
  float: right;
  display: none;
}

.nav__hamburger:hover {
  cursor: pointer;
}

.nav__closeBtn__wrapper {
  font-size: 40px;
  position: absolute;
  font-family: "Lato", serif;
  top: 0;
  line-height: 40px;
  right: 0;
  padding: 20px 60px;
  margin: 0;
  display: none;
}

.nav__closeBtn__wrapper:hover {
  cursor: pointer;
}

.nav__closeBtn__wrapper:hover path {
  color: #ee403a;
}

@media only screen and (max-width: 1050px) {
  .nav__hamburger {
    display: inline-block;
  }

  .nav__linkContainerInner {
    margin-top: calc(50vh - 230px);
  }

  .nav__rightContainer {
    float: none;
    color: black;
  }

  .nav__rightContainer__link {
    margin: 0;
    padding: 25px 40px;
    font-family: "Lato", serif;
    font-weight: 600;
    color: rgb(0, 0, 0);
    z-index: 10000;
    width: 3000px;
    position: relative;
    margin: auto;
    display: block;
    font-size: 40px;
    text-align: center;
    height: 110px;
  }

  .nav__linkContainer {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background: rgba(0, 0, 0, 0.9);
    color: rgb(0, 0, 0);
    z-index: 7000;
    box-sizing: border-box;
  }

  .nav__link {
    display: block;
    font-size: 40px;
    text-align: center;
    height: 110px;
    color: rgb(0, 0, 0);
  }

  .nav__link:hover .nav__underline {
    display: none;
  }

  .nav__rightContainer__link:hover {
    background: none;
    color: #2c2b25;
  }

  .nav__closeBtn__wrapper {
    display: block;
  }

  .nav__hideMobileMenu {
    display: none;
  }

  .nav__active {
    background: none;
  }
}

@media only screen and (max-width: 700px) {
  .navContainer {
    width: 80vw;
  }
}

.parallax {
  -webkit-perspective: 100px;
          perspective: 100px;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 50%;
  right: 0;
  bottom: 0;
  margin-left: -1500px;
}

.parallax__overflow {
  -webkit-perspective: 100px;
          perspective: 100px;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 50%;
  right: 0;
  bottom: 0;
  margin-left: -1500px;
  overflow-x: hidden;
  overflow-y: auto;
}

.parallax__layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.beforecover{
  z-index: 200000;
}
.parallax__layer .parallax__img {
  display: block;
  position: absolute;
  bottom: 0;
}

.parallax__cover {
  background: #FED978;
  display: block;
  position: absolute;
  width: 3000px;
  top: calc(100% - 2px);
  left: 0;
  right: 0;
  height: 2000px;
  z-index: 200000;
  width: 3000px;
}

.parallax__layer__0 {
  -webkit-transform: translateZ(-300px) scale(4);
          transform: translateZ(-300px) scale(4);
}

.parallax__layer__1 {
  -webkit-transform: translateZ(-250px) scale(3.5);
          transform: translateZ(-250px) scale(3.5);
  z-index: 10000;
}

.parallax__layer__2 {
  -webkit-transform: translateZ(-200px) scale(3);
          transform: translateZ(-200px) scale(3);
}

.parallax__layer__3 {
  -webkit-transform: translateZ(-150px) scale(2.5);
          transform: translateZ(-150px) scale(2.5);
}

.parallax__layer__4 {
  -webkit-transform: translateZ(-100px) scale(2);
          transform: translateZ(-100px) scale(2);
}

.parallax__layer__5 {
  -webkit-transform: translateZ(-50px) scale(1.5);
          transform: translateZ(-50px) scale(1.5);
}
.parallax__layer__6 {
  z-index: 300000;
}
/* Allow compatibility on IE and Edge without parallax scroll */
_:-ms-lang(x),
.parallax__layer__0,
.parallax__layer__1,
.parallax__layer__2,
.parallax__layer__3,
.parallax__layer__4,
.parallax__layer__5 {
  transform: none;
}

.landingImagesLoading {
  opacity: 1;
  position: fixed;
  top: 0;
  left: 0;
  margin: auto;
  height: 3000px;
  width: 3000px;
  background: #FED978;
  z-index: 1000;
}

.landingImagesLoaded {
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  background: #FED978;
  height: 3000px;
  width: 3000px;
  z-index: 0;
  transition: all 2s;
}

@-webkit-keyframes float {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
  50% {
    -webkit-transform: translate(0px, 15px);
            transform: translate(0px, 15px);
  }
  100% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
}

@keyframes float {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
  50% {
    -webkit-transform: translate(0px, 15px);
            transform: translate(0px, 15px);
  }
  100% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
}

